import axios from  "axios"
import Cookies from "js-cookie"
import {FiltersType} from "../BLL/Reducers/rimsReducer"
import { BrandType, RimType,UsualFilterType } from "../Types/RimsTypes";
const csrftoken = Cookies.get('csrftoken');


let instance=axios.create({
    withCredentials:true,
    baseURL:"http://127.0.0.1:8000/api/",
    headers:{
        'X-CSRFToken': csrftoken
    }
})

type GetRimsType={
    count: number,
    next: null | boolean,
    previous: null | boolean,
    results:Array<RimType> 
}
export let getRims=(filters:FiltersType,page:number,is_sunGlasses=false)=>{
    return instance.get<GetRimsType>(`glassesrims/?is_sunglasses=${is_sunGlasses}&is_available=${filters.is_available}&brand=${filters.brand}&color=${filters.color}&shape=${filters.shape}&material=${filters.material}&gender=${filters.gender}&sort_price=${filters.sort_by_price}`)
}
export let allParamsRequests={
    getColors(){
        return instance.get<Array<UsualFilterType>>("colors/")
    },
    getMaterials(){
        return instance.get<Array<UsualFilterType>>("materials/")
    },
    getShapes(){
        return instance.get<Array<UsualFilterType>>("shapes/")
    },
    getBrands(){
        return instance.get<Array<BrandType>>("brands/")
    },
    getGenders(){
        return instance.get<Array<UsualFilterType>>("genders/")
    },
    getCategorysAccessorys(){
        return instance.get<Array<UsualFilterType>>("categoryaccessory/")
    }
}

export default instance
